/** 排序条件 */
export default class Schedule {
  public static build(data: Schedule) {
    const schedule: Schedule = new Schedule()
    schedule.jobId = data.jobId
    schedule.beanName = data.beanName
    schedule.methodName = data.methodName
    schedule.params = data.params
    schedule.cronExpression = data.cronExpression
    schedule.createTime = data.createTime
    schedule.status = data.status
    schedule.remark = data.remark
    return schedule
  }

  /** ID */
  public jobId: string = ''
  /** bean名称 */
  public beanName: string = ''
  /** 方法名称 */
  public methodName: string = ''
  /** 参数 */
  public params: string = ''
  /** cron表达式 */
  public cronExpression: string = ''
  /** 创建时间 */
  public createTime: string = ''
  /** 状态 */
  public status: number = 0
  /** 备注 */
  public remark: string = ''
  /** 索引 */
  readonly [id: string]: string | number
}
