import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { Schedule } from '@/domains/schedule'

interface IJobIds {
  jobIds: string[]
}

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/schedule/list',
    method: 'get',
    params: query
  })
}

export function saveData(data: Schedule) {
  return request({
    url: '/sys/schedule/save',
    method: 'post',
    data
  })
}

export function fetchEditInfo(id: string) {
  const url = '/sys/schedule/info/' + id
  return request({
    url,
    method: 'get'
  })
}

export function updateData(data: Schedule) {
  return request({
    url: '/sys/schedule/update',
    method: 'put',
    data
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/sys/schedule/delete',
    method: 'delete',
    data
  })
}

export function pauseData(data: IJobIds) {
  return request({
    url: '/sys/schedule/pause',
    method: 'get',
    data
  })
}

export function resumeData(data: IJobIds) {
  return request({
    url: '/sys/schedule/resume',
    method: 'get',
    data
  })
}
