




































































































import { Component, Vue } from 'vue-property-decorator'
import TableList from '@/components/TableList/index.vue'
import Pagination from '@/components/Pagination/index.vue'
import Edit from './edit.vue'
import { fetchList, deleteData, pauseData, resumeData } from '@/api/sysManage/scheduleManage'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Sort } from '@/domains/sort'
import { Schedule } from '@/domains/schedule'
import { Pages } from '@/domains/pages'
import { checkSelection } from '@/utils/checkSelection'
import sortable from '@/utils/sortable'

class Query extends ListQuery {
  public beanName: string = ''
}

class Page extends Pages {
  public list: Schedule[] = []
}

@Component({
  name: 'ScheduleManage',
  components: {
    TableList,
    Pagination,
    Edit
  },
  filters: {
    statusFilter(status: number) {
      const statusMap = ['success', 'danger']
      return statusMap[status]
    },
    statusTextFilter(status: number) {
      const statusMap = ['正常', '暂停']
      return statusMap[status]
    }
  }
})

export default class ScheduleManage extends Vue {
  /** 是否显示table列表 */
  private isShowList: boolean = true
  /** 查询条件 */
  private listQuery: Query = new Query()
  /** 数据列表 */
  private list: Schedule[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: '任务ID',
      key: 'jobId'
    },
    {
      label: 'bean名称',
      key: 'beanName'
    },
    {
      label: '方法名称',
      key: 'methodName'
    },
    {
      label: '参数',
      key: 'params'
    },
    {
      label: 'cron表达式',
      key: 'cronExpression'
    },
    {
      label: '备注',
      key: 'remark'
    },
    {
      label: '状态',
      key: 'status',
      slot: 'status'
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前编辑数据的ID */
  private id: string = '0'
  /** 当前选中的数据 */
  private multipleSelection: Schedule[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'jobId'
  /** tab标题，编辑或者新增 */
  get tabLabel() {
    return this.type === 'edit' ? this.$t('common.edit') : this.$t('common.create')
  }

  private created(): void {
    this.getList()
  }

  /** 获取数据列表 */
  private getList(): void {
    fetchList(this.listQuery).then((response: any) => {
      const page: Page = response.page
      this.list = page.list
      this.total = page.totalCount

      // this.newList = this.list.map((v: Role) => v.roleId)
      this.newList = this.list.map((v: Schedule) => v[this.idKey] as string)

      const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      sortable(el, this.list, this.newList)
    })
  }
  /** 搜索 */
  private handleFilter(): void {
    this.listQuery._search = true
    this.listQuery.page = 1
    this.getList()
  }
  /** 新增 */
  private handleCreate(): void {
    this.type = 'save'
    this.isShowList = false
  }
  /** 编辑 */
  private handleEdit(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.id = this.multipleSelection[0][this.idKey] as string
      this.type = 'edit'
      this.isShowList = false
    })
  }
  /** 删除数据 */
  private handleDelete(): void {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('是否确定删除该数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids: string[] = []
        this.multipleSelection.map(item => {
          ids.push(item[this.idKey] as string)
        })
        deleteData(ids).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.handleFilter()
        })
      }).catch(() => {})
    })
  }
  /** 排序 */
  private handleSortChange(sort: Sort): void {
    if (sort.sidx !== undefined) {
      this.listQuery.sidx = sort.sidx
    }
    this.listQuery.order = sort.order
    this.handleFilter()
  }
  private handleSelectionChange(val: Schedule[]): void {
    this.multipleSelection = val
  }
  /** 监听子组件，执行方法 */
  private getMsgFromChild(v: string): void {
    if (v !== 'cancel') {
      this.handleFilter()
    }
    this.isShowList = true
  }
  // 暂停任务
  private handlePause() {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('确定要暂停选中的记录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postPauseData()
      }).catch(() => {})
    })
  }
  // 确定暂停任务
  private postPauseData() {
    const ids: string[] = []
    this.multipleSelection.map(item => {
      ids.push(item.jobId)
    })
    pauseData({ jobIds: ids }).then(response => {
      this.$message({
        type: 'success',
        message: '暂停成功!'
      })
      this.handleFilter()
    })
  }
  // 开始任务
  private handleResume() {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('确定要开始选中的记录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postResumeData()
      }).catch(() => {})
    })
  }
  // 确定开始任务
  private postResumeData() {
    const ids: string[] = []
    this.multipleSelection.map(item => {
      ids.push(item.jobId)
    })
    resumeData({ jobIds: ids }).then(response => {
      this.$message({
        type: 'success',
        message: '开始成功!'
      })
      this.handleFilter()
    })
  }
}
